import {NJSSandbox} from './Application';

export class NJSViewController{
    public divHost: HTMLDivElement;
    public div: HTMLDivElement;
    public njsSandbox: NJSSandbox;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;

        this.div = document.createElement('div');
        this.div.style.width = '100%';
        this.div.style.height = '100%';
        this.div.style.background = '#333';
        this.divHost.appendChild(this.div);

        if(this.divHost) {
            this.njsSandbox = new NJSSandbox(this.div);
        }

    }
    async initApp(div: HTMLElement) {
        return import('./Application').then(({ NJSSandbox }) => {
            if(div) {
                this.njsSandbox = new NJSSandbox(div);
            }
        }).catch((err) => { console.log('Stop loading Apps.'); console.log(err); });
    }
    public destroy() {
        this.njsSandbox.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}


